<template>
  <div id="LoginPage" class="login-page">
    <van-nav-bar
      title="会员登录"
      left-arrow
      @click-left="goHome"
    />

    <div class="div-header"></div>

    <div class="container div-content">
      <div class="container-fuild ContactUs-container">
        <div class="row">
          <div class="col-xs-12 col-sm-12 col-md-6">
            <form class="form-horizontal" role="form" @submit.prevent="submitForm">
              <div class="form-group">
                <label for="name" class="col-sm-2 control-label">手机号</label>
                <div class="col-sm-10 col-xs-12">
                  <input type="text" class="form-control" id="name" placeholder="请输入" v-model="phoneNumber">
                </div>
              </div>

              <!-- 添加密码输入框 -->
              <div class="form-group">
                <label for="password" class="col-sm-2 control-label">密码</label>
                <div class="col-sm-10 col-xs-12">
                  <input type="password" class="form-control" id="password" placeholder="请输入" v-model="password">
                </div>
              </div>

              <div class="form-group">
                <div class="col-sm-offset-2 col-sm-10">
                  <button type="submit" class="btn btn-default btn-block">提交</button>
                </div>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import rawData from '@/utils/data.json';
export default {
  name: 'LoginPage',
  data() {
    return {
      phoneNumber: '', // 存储手机号
      password: '',   // 存储密码
    }
  },
  methods: {
    submitForm() {
      // 执行表单提交时的逻辑
      if (!this.phoneNumber.match(/^1[3456789]\d{9}$/)) {
        // 手机号格式不正确
        alert('请输入正确的手机号码');
        return;
      }

      if (!this.password) {
        // 密码为空
        alert('密码必填');
        return;
      }

      // 获取手机号和密码
      const phoneNumber = this.phoneNumber;
      if (this.checkAccount()) {
        this.$router.push({
          name: 'MemberCenter',
          query: {
            phoneNumber,
          }
        })
      } else {
        alert('账号或密码错误')
      }
    },
    // 校验账号密码
    checkAccount() {
      const accounts = rawData.accounts || {}
      const phone = this.phoneNumber
      const password = this.password
      const savedPassword = accounts[phone]
      const confirm = phone && (password === savedPassword)
      return confirm
    },
    goHome() {
      this.$router.push({
        name: 'HomePage',
      })
    }
  }
}
</script>

<style scoped>
.login-page >>> .van-nav-bar {
  background-color: #977961;
}

.login-page >>> .van-nav-bar__title {
  color: #fff;
}

.login-page >>> .van-icon-arrow-left {
  color: #fff;
}

.login-page {
  background-color: #fff;
}

.div-header {
  position: relative;
  width: 100%;
  height: 100px;
  margin: 20px 0;
  background: no-repeat center;
  background-size: contain;
  background-image: url("../assets/img/logo.jpg");
}

.div-content {
  padding: 20px;
}
</style>



